:root {
  --overlay-color: rgba(255, 255, 255, 0.5);
  --splash-color: #ffffff;
}
.dark-side {
  --overlay-color: rgba(0, 0, 0, 0.5);
  --splash-color: #000000;
}
.overlay {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: var(--overlay-color);
  z-index: 1040;
}
.overlay.transparent {
  background-color: rgba(255, 255, 255, 0) !important;
}
.global-overlay {
  z-index: 2000 !important;
}
.global-dialog {
  z-index: 2001 !important;
}
.splashscreen {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  z-index: 2147483647;
  background: var(--splash-color);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
