:root {
  --step-list-number-color: #757575;
}
.dark-side {
  --step-list-number-color: #ffedbc;
}
.step-list {
  margin: 0 0 0 3em;
  padding: 0;
  list-style-type: none;
  counter-reset: li;
}
.step-list > li {
  border-left: 1px var(--border-color) solid;
  position: relative;
  padding: 0 1rem;
  margin: 1rem;
  vertical-align: top;
}
.step-list > li::before {
  position: absolute;
  content: counter(li);
  counter-increment: li;
  font-size: 2rem;
  color: var(--step-list-number-color);
  left: -0.5rem;
  top: 0;
  margin-left: -2em;
  width: 1.8em;
  text-align: right;
}
.custom-list-marker {
  list-style: none inside;
}
.custom-list-marker li {
  position: relative;
  padding-left: 1.2em;
}
.custom-list-marker li::before {
  content: attr(data-marker);
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
.items-list,
.feed-list {
  margin: 0;
  padding: 4px;
  position: relative;
  display: block;
  list-style: none inside;
}
.items-list .item,
.feed-list .item,
.items-list > li,
.feed-list > li {
  display: flex;
  flex-flow: column;
  justify-content: center;
  position: relative;
  height: 56px;
  padding: 8px 16px;
  vertical-align: middle;
  overflow: hidden;
  cursor: default;
}
.items-list .item .avatar,
.feed-list .item .avatar,
.items-list > li .avatar,
.feed-list > li .avatar {
  width: 42px;
  height: 42px;
  border-radius: 50%;
  position: absolute;
  left: 16px;
  top: 50%;
  margin-top: -21px;
  font-size: 32px;
  line-height: 42px;
}
.items-list .item .second-action,
.feed-list .item .second-action,
.items-list > li .second-action,
.feed-list > li .second-action {
  position: absolute;
  right: 16px;
  width: 24px;
  height: 24px;
  font-size: 24px;
  line-height: 24px;
  top: 50%;
  margin-top: -12px;
  cursor: pointer;
}
.items-list .item .label,
.feed-list .item .label,
.items-list > li .label,
.feed-list > li .label,
.items-list .item .second-label,
.feed-list .item .second-label,
.items-list > li .second-label,
.feed-list > li .second-label {
  display: block;
}
.items-list .item .label,
.feed-list .item .label,
.items-list > li .label,
.feed-list > li .label {
  line-height: 1;
}
.items-list .item .second-label,
.feed-list .item .second-label,
.items-list > li .second-label,
.feed-list > li .second-label {
  font-size: 0.9em;
  color: #515151;
  line-height: 1;
}
.items-list .item .avatar ~ .label,
.feed-list .item .avatar ~ .label,
.items-list > li .avatar ~ .label,
.feed-list > li .avatar ~ .label,
.items-list .item .avatar ~ .second-label,
.feed-list .item .avatar ~ .second-label,
.items-list > li .avatar ~ .second-label,
.feed-list > li .avatar ~ .second-label {
  padding-left: 56px;
}
.items-list .item + .item,
.feed-list .item + .item {
  border-top: 1px var(--border-color) solid;
}
.items-list > li + li,
.feed-list > li + li {
  border-top: 1px var(--border-color) solid;
}
.feed-list .title {
  padding: 0 16px;
  font-size: 18px;
  font-weight: 500;
  height: 36px;
  line-height: 36px;
  display: block;
}
.feed-list .item + .title,
.feed-list li + .title {
  margin-top: 1rem;
}
.feed-list .title + .item,
.feed-list .title + li,
.feed-list li + .title {
  border-top: 1px transparent solid;
}
.feed-list .item,
.feed-list > li {
  height: 104px;
  padding: 16px 0;
  justify-content: space-around;
}
.feed-list .item .avatar,
.feed-list > li .avatar {
  position: absolute;
  top: 16px;
  left: 16px;
  width: 96px;
  height: 72px;
  border-radius: 4px;
  overflow: hidden;
  margin-top: 0;
  object-fit: cover;
}
.feed-list .item .second-label,
.feed-list > li .second-label {
  font-size: 10px;
}
.feed-list .item .avatar ~ .label,
.feed-list > li .avatar ~ .label,
.feed-list .item .avatar ~ .second-label,
.feed-list > li .avatar ~ .second-label {
  margin-left: 128px;
  margin-right: 16px;
  padding-left: 0;
}
.feed-list .item .avatar.on-right,
.feed-list > li .avatar.on-right {
  float: none;
  left: auto;
  right: 16px;
}
.feed-list .item .avatar.on-right ~ .label,
.feed-list > li .avatar.on-right ~ .label,
.feed-list .item .avatar.on-right ~ .second-label,
.feed-list > li .avatar.on-right ~ .second-label {
  margin-left: 16px;
  margin-right: 128px;
}
.group-list {
  margin: 0;
  position: relative;
  display: block;
  list-style: none inside;
  border: 1px solid var(--border-color);
}
.group-list .item,
.group-list > li {
  display: block;
  position: relative;
  padding: 4px 8px;
}
.group-list .item + .item,
.group-list > li + li {
  border-top: 1px solid var(--border-color);
}
.group-list.horizontal {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.group-list.horizontal .item,
.group-list.horizontal > li {
  flex-basis: 100%;
}
.group-list.horizontal .item + .item,
.group-list.horizontal > li + li {
  border-top: none;
  border-left: 1px solid var(--border-color);
}
.inline-list {
  list-style: none;
  padding-left: 0;
  margin-left: 0;
}
.inline-list li {
  display: inline-block;
  margin-right: 0.625rem;
}
.inline-list li:last-child {
  margin-right: 0;
}
.unstyled-list {
  list-style: none inside;
  margin-left: 0;
}
ol.decimal {
  list-style-type: decimal;
}
ol.roman-upper {
  list-style-type: upper-roman;
}
ol.roman-lower {
  list-style-type: lower-roman;
}
ol.alpha-upper {
  list-style-type: upper-alpha;
}
ol.alpha-lower {
  list-style-type: lower-alpha;
}
ol.latin-upper {
  list-style-type: upper-latin;
}
ol.latin-lower {
  list-style-type: lower-latin;
}
.no-style {
  list-style-type: none;
}
