.lightbox-overlay {
  background-color: rgba(0, 0, 0, 0.9);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2000;
}
.lightbox {
  z-index: 2001;
  position: fixed;
  background: transparent;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}
.lightbox__closer {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 32px;
  height: 32px;
  background: rgba(248, 248, 248, 0.5);
  color: #1d1d1d;
  font-family: "Segoe UI Symbol", serif;
  border-radius: 4px;
}
.lightbox__closer * {
  line-height: 32px;
  font-size: 20px;
  height: 20px;
  width: 20px;
}
.lightbox__prev,
.lightbox__next {
  font-family: "Segoe UI Symbol", serif;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: rgba(248, 248, 248, 0.5);
  color: #1d1d1d;
  flex-grow: 0;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 50%;
  margin-top: -24px;
  left: 20px;
  font-size: 36px;
}
.lightbox__prev *,
.lightbox__next * {
  line-height: 40px;
  font-size: 36px;
  height: 40px;
  width: 40px;
  object-fit: cover;
}
.lightbox__next {
  left: auto;
  right: 20px;
}
.lightbox__prev,
.lightbox__next,
.lightbox__closer {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 2;
}
.lightbox__image {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
}
.lightbox__image-wrapper {
  display: block;
  width: auto;
  max-height: calc(100vh - 80px);
  max-width: calc(100vw - 20px);
}
.lightbox__image-wrapper::before {
  content: attr(data-title);
  position: absolute;
  color: #ffffff;
  top: 100%;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
}
.lightbox__image-wrapper.no-title::before {
  display: none;
}
.lightbox__image-wrapper img {
  display: block;
  width: auto;
  max-height: calc(100vh - 80px);
  max-width: calc(100vw - 20px);
}
