:root {
  --info-box-border-radius: 6px;
  --info-box-background: var(--default-background);
  --info-box-color: var(--default-color);
  --info-box-border-color: var(--border-color);
}
.dark-side {
  --info-box-background: var(--default-background);
  --info-box-color: var(--default-color);
  --info-box-border-color: var(--border-color);
}
.info-box {
  position: fixed;
  background: var(--info-box-background);
  color: var(--info-box-color);
  border: 1px solid var(--info-box-border-color);
  border-radius: var(--info-box-border-radius);
  display: block;
  z-index: 1050;
  max-width: calc(100vw - 100px);
  max-height: calc(100vh - 100px);
}
.info-box .closer {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 4px;
  right: 4px;
  background-color: inherit;
  color: inherit;
  z-index: 100;
  border: none;
}
.info-box .closer::before {
  display: block;
  content: "\00d7";
  font-size: 24px;
}
.info-box .info-box-content {
  padding: 20px;
  z-index: 0;
}
.info-box2 {
  display: flex;
  flex-direction: column;
  position: relative;
  border-radius: var(--info-box-border-radius);
  border: 1px solid var(--info-box-border-color);
  background: var(--info-box-background);
  color: var(--info-box-color);
  padding: 4px 8px;
}
.info-box2 .title {
  text-transform: uppercase;
  font-size: 9px;
  font-weight: 400;
}
.info-box2 .value {
  display: flex;
  font-size: 16px;
  font-weight: 500;
  align-items: flex-end;
  justify-content: space-between;
}
.info-box2 .value .primary-value {
  font-size: 1em;
}
.info-box2 .value .secondary-value {
  font-size: 0.8em;
  margin-left: auto;
}
:root {
  --more-info-box-border-radius: 6px;
  --more-info-box-background: var(--default-background);
  --more-info-box-color: var(--default-color);
  --more-info-box-icon-color: rgba(0, 0, 0, 0.15);
  --more-info-box-more-background: rgba(0, 0, 0, 0.1);
  --more-info-box-more-color: rgba(255, 255, 255, 0.8);
  --more-info-box-border-color: var(--border-color);
}
.dark-side {
  --more-info-box-background: linear-gradient(to right, rgba(255, 238, 238, 0.15), rgba(255, 252, 252, 0.1));
  --more-info-box-color: var(--default-color);
  --more-info-box-icon-color: rgba(0, 0, 0, 0.15);
  --more-info-box-more-background: rgba(0, 0, 0, 0.1);
  --more-info-box-more-color: rgba(255, 255, 255, 0.8);
  --more-info-box-border-color: var(--border-color);
}
.more-info-box {
  height: 128px;
  display: flex;
  flex-flow: row wrap;
  background: var(--more-info-box-background);
  color: var(--more-info-box-color);
  border-radius: var(--more-info-box-border-radius);
  overflow: hidden;
  position: relative;
  border: 1px solid var(--more-info-box-border-color);
}
.more-info-box .icon {
  position: absolute;
  top: 1rem;
  right: 1rem;
  height: 64px;
  width: 64px;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.3s ease;
  color: var(--more-info-box-icon-color);
}
.more-info-box .icon * {
  font-size: 64px;
  width: 64px;
  height: 64px;
  line-height: 1;
}
.more-info-box .content {
  position: relative;
  line-height: 1.2;
  display: block;
  padding: 8px 16px;
  max-width: calc(100% - 106px);
}
.more-info-box .title {
  font-size: 16px;
  font-weight: 500;
}
.more-info-box .value {
  font-size: 38px;
  font-weight: 700;
}
.more-info-box .more {
  width: 100%;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  background: var(--more-info-box-more-background);
  text-decoration: none;
  position: relative;
  padding: 4px 8px;
  margin: 0;
  font-size: 12px;
  color: inherit;
}
.more-info-box:hover .icon {
  transform: scale(1.2);
  transition: transform 0.3s ease;
}
