:root {
  --icon-box-border-radius: 6px;
  --icon-box-background: var(--default-background);
  --icon-box-color: var(--default-color);
  --icon-box-border-color: var(--border-color);
  --icon-box-icon-background: #f4f4f4;
  --icon-box-icon-color: var(--default-color);
}
.dark-side {
  --icon-box-background: var(--default-background);
  --icon-box-color: #dbdfe7;
  --icon-box-border-color: var(--border-color);
  --icon-box-icon-background: #515151;
  --icon-box-icon-color: var(--default-color);
}
.icon-box,
.icon-box2,
.icon-box3 {
  height: 90px;
  display: flex;
  flex-flow: row nowrap;
  background: var(--icon-box-background);
  color: var(--icon-box-color);
  border: 1px solid var(--icon-box-border-color);
  border-radius: var(--icon-box-border-radius);
  overflow: hidden;
  position: relative;
}
.icon-box .icon,
.icon-box2 .icon,
.icon-box3 .icon,
.icon-box .content,
.icon-box2 .content,
.icon-box3 .content {
  position: relative;
}
.icon-box .title,
.icon-box2 .title,
.icon-box3 .title {
  font-size: 16px;
  font-weight: 500;
  white-space: nowrap;
}
.icon-box .subtitle,
.icon-box2 .subtitle,
.icon-box3 .subtitle {
  font-size: 14px;
  font-weight: 500;
  border-top: 1px solid var(--icon-box-border-color);
  white-space: nowrap;
  padding-top: 6px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.icon-box .value,
.icon-box2 .value,
.icon-box3 .value {
  font-size: 24px;
  font-weight: 700;
  margin: 4px 0 10px 0;
  white-space: nowrap;
}
.icon-box .icon {
  height: 90px;
  width: 90px;
  flex-shrink: 0;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--icon-box-icon-background);
}
.icon-box .icon > * {
  font-size: 45px;
  width: 45px;
  height: 45px;
  line-height: 1;
}
.icon-box .content {
  line-height: 1.2;
  display: block;
  width: 100%;
  background-color: inherit;
  padding: 1rem;
}
.icon-box .title {
  text-transform: uppercase;
}
.icon-box2,
.icon-box3 {
  height: auto;
}
.icon-box2 .icon,
.icon-box3 .icon {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: var(--icon-box-icon-background);
  color: var(--icon-box-icon-color);
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  border-radius: 50%;
}
.icon-box2 .icon *,
.icon-box3 .icon * {
  font-size: 24px;
  width: 24px;
  height: 24px;
}
.icon-box2 .content,
.icon-box3 .content {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  width: 100%;
}
.icon-box2 .title,
.icon-box3 .title {
  order: 1;
}
.icon-box2 .value,
.icon-box3 .value {
  order: 2;
  margin: 0;
}
.icon-box2 .subtitle,
.icon-box3 .subtitle {
  order: 3;
  margin-top: 1rem;
}
.icon-box3 .icon {
  right: auto;
  left: 1rem;
  border-radius: 6px;
}
.icon-box3 .title,
.icon-box3 .value {
  text-align: right;
  margin-left: 60px;
}
.icon-box3 .value {
  order: 1;
}
.icon-box3 .title {
  order: 2;
}
.icon-box3 .subtitle {
  order: 3;
}
.icon-box4 {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  position: relative;
  border-radius: var(--icon-box-border-radius);
  border: 1px solid var(--icon-box-border-color);
  background: var(--icon-box-background);
  color: var(--icon-box-color);
  padding: 1rem;
}
.icon-box4 .icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 64px;
  height: 64px;
  overflow: hidden;
  border-radius: 50%;
  border: 1px solid var(--icon-box-border-color);
  background: var(--icon-box-icon-background);
  color: var(--icon-box-icon-color);
  margin-right: 1rem;
  flex-shrink: 0;
  aspect-ratio: 1/1;
}
.icon-box4 .icon * {
  font-size: 60px;
  width: 100%;
  height: 100%;
  text-align: center;
  vertical-align: middle;
}
.icon-box4 .content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.icon-box4 .content .title {
  font-size: 12px;
  font-weight: 400;
}
.icon-box4 .content .value {
  font-size: 16px;
  font-weight: 700;
}
