:root {
  --toast-background: #323232;
  --toast-color: #ffffff;
  --toast-border-radius: 6px;
  --toast-closer-background: inherit;
  --toast-closer-color: inherit;
  --toast-closer-background-hover: var(--color-alert);
  --toast-closer-color-hover: var(--color-light);
}
.dark-side {
  --toast-background: #2b2d30;
  --toast-color: #ffffff;
}
.toast {
  position: fixed;
  bottom: 20px;
  width: auto;
  height: auto;
  max-width: 35.5rem;
  padding: 0.5rem 1.5rem;
  text-align: left;
  font-size: 0.875rem;
  font-weight: normal;
  background: var(--toast-background);
  color: var(--toast-color);
  border-radius: var(--toast-border-radius);
  z-index: 1080;
  display: block;
}
.toast.show-top {
  bottom: auto;
  top: 20px;
}
.toast.show-center {
  left: 50%;
  top: 50%;
  bottom: auto;
  transform: translate(-50%, -50%);
}
.toast .closer {
  display: block;
  position: absolute;
  top: 4px;
  right: 4px;
  background: var(--toast-closer-background);
  color: var(--toast-closer-color);
  z-index: 100;
}
.toast .closer::before {
  font-family: "Segoe UI Symbol", serif;
  display: block;
  content: "❌";
  font-size: 24px;
  margin-top: -4px;
}
