.fixed-top,
.fixed-bottom {
  position: fixed;
  left: 0;
  right: 0;
  width: 100%;
}
.fixed-top {
  top: 0;
  bottom: auto;
}
.fixed-bottom {
  top: auto;
  bottom: 0;
}
.pos-relative {
  position: relative!important;
}
.pos-absolute {
  position: absolute!important;
}
.pos-static {
  position: static!important;
}
.pos-fixed {
  position: fixed!important;
}
.pos-sticky {
  position: sticky!important;
  top: 0;
}
