.image-grid {
  display: flex;
  flex-flow: row wrap;
}
.image-grid__item {
  display: block;
  overflow: hidden;
}
.image-grid__item-landscape {
  width: 320px;
  height: 180px;
}
.image-grid__item-portrait {
  width: 160px;
  height: 180px;
}
.image-grid__item img {
  display: block;
  width: 100%;
  height: 100%;
}
.image-grid.half-size .image-grid__item-landscape {
  width: 160px;
  height: 90px;
}
.image-grid.half-size .image-grid__item-portrait {
  width: 80px;
  height: 90px;
}
