:root {
  --taginput-border-radius: 4px;
  --taginput-color: var(--input-color);
}
.dark-side {
  --taginput-color: var(--input-color);
}
.tag-input {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  padding: 0 2px;
  cursor: text;
  position: relative;
  height: auto;
  min-height: 36px;
  border-radius: var(--taginput-border-radius);
  overflow: hidden;
}
.tag-input .original-input {
  width: 1px;
  height: 1px;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}
.tag-input .input-wrapper {
  width: auto;
  border: none!important;
  height: 26px;
  line-height: 26px;
  font-size: 14px;
  padding: 0 4px;
  background: transparent;
  color: var(--taginput-color);
}
.tag-input .input-wrapper:focus {
  box-shadow: none!important;
}
.tag-input .input-clear-button {
  font-family: "Segoe UI Symbol", serif;
  position: absolute;
  top: 2px;
  right: 2px;
  z-index: 2;
  background-color: transparent;
  height: 30px;
  width: 30px;
}
.tag-input.padding-for-clear {
  padding-right: 48px;
}
.tag-input.input-large {
  min-height: 50px;
}
.tag-input.input-large input {
  height: 48px;
  font-size: 1.3rem;
  padding: 0 10px;
  line-height: 48px;
  min-width: 5em;
}
.tag-input.input-large .button {
  height: 48px;
  min-width: 48px;
  font-size: 1.3rem;
}
.tag-input.input-large .button [class*=mif-] {
  font-size: 1.3rem;
}
.tag-input.input-large .button .input-clear-button {
  width: 48px;
  padding: 0;
}
.tag-input.input-small {
  min-height: 28px;
}
.tag-input.input-small input {
  height: 26px;
  font-size: 0.9rem;
  padding: 0 4px;
  line-height: 26px;
  min-width: 5em;
}
.tag-input.input-small .button {
  height: 26px;
  min-width: 26px;
  font-size: 0.9rem;
}
.tag-input.input-small .button [class*=mif-] {
  font-size: 0.9rem;
}
.tag-input.input-small .input-clear-button {
  width: 26px;
  padding: 0;
  font-family: "Segoe UI Symbol", serif;
}
.tag-input.static-mode {
  border: none!important;
  cursor: default;
}
.tag-input.static-mode .button,
.tag-input.static-mode input {
  display: none!important;
}
.tag-input .autocomplete-list {
  margin: 0;
  padding: 0;
  list-style: none inside;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  overflow: hidden;
  overflow-y: auto;
  background-color: #ffffff;
  height: auto;
  border: 1px solid var(--border-color);
  z-index: 3;
  display: block;
}
.tag-input .autocomplete-list .item {
  display: block;
  position: relative;
  padding: 4px 8px;
  cursor: pointer;
}
.tag-input .autocomplete-list .item:hover {
  background-color: rgba(29, 29, 29, 0.1);
}
.tag-input .autocomplete-list .item + .item {
  border-top: 1px solid var(--border-color);
}
