.date-picker input {
  width: 1px;
  height: 1px;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}
.date-picker input:focus ~ .time-wrapper {
  box-shadow: 0 0 0 3px rgba(244, 244, 244, 0.45);
}
.date-picker .date-wrapper {
  display: inline-flex;
  flex-flow: row nowrap;
  position: relative;
  border: 1px solid var(--border-color);
  width: 100%;
  overflow: hidden;
  height: 100%;
  border-radius: var(--wheel-picker-border-radius);
}
.date-picker .month,
.date-picker .day,
.date-picker .year {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
  border: 1px solid var(--border-color);
  text-align: center;
  height: 100%;
}
.date-picker .month {
  flex-grow: 2;
}
.date-picker .sel-month {
  flex-grow: 2!important;
}
.date-picker .action-block {
  border: 1px solid var(--border-color);
  border-radius: var(--datepicker-border-radius);
}
.date-picker .button {
  margin: 2px;
}
.date-picker .button .icon {
  font-family: "Segoe UI Symbol", serif;
}
.date-picker li {
  cursor: pointer;
}
