:root {
  --thumb-background: rgba(255, 255, 255, 0.8);
  --img-overlay-background: rgba(70, 140, 255, 0.7);
  --img-overlay-color: #ffffff;
  --img-border-radius: 6px;
}
.dark-side {
  --thumb-background: rgba(0, 0, 0, 0.8);
  --img-overlay-background: rgba(70, 140, 255, 0.7);
  --img-overlay-color: #ffffff;
}
.img-container,
.img-fluid,
.img-thumbnail {
  width: 100%;
  height: auto;
  display: block;
  position: relative;
  vertical-align: middle;
  background-color: transparent;
  transition: all 0.3s ease-in-out;
  overflow: hidden;
  border-radius: var(--img-border-radius);
}
.img-thumbnail {
  padding: 0.25rem;
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  background-color: var(--thumb-background);
}
.img-container {
  display: block;
}
.img-container img {
  width: 100%;
  height: auto;
  position: relative;
  object-fit: cover;
}
.img-container .image-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0;
  overflow: hidden;
  font-size: 0.875rem;
  line-height: 1rem;
  padding: 2em;
  background-color: var(--img-overlay-background);
  color: var(--img-overlay-color);
  text-align: center;
  border-radius: inherit;
  transition: all 0.3s ease-in-out;
}
.img-container .image-overlay * {
  color: inherit;
}
@media (hover: hover) {
  .img-container .image-overlay:hover {
    opacity: 1;
  }
  .img-container .image-overlay:hover:before,
  .img-container .image-overlay:hover:after {
    opacity: 1;
    transform: scale(1);
  }
}
@media (hover: none) {
  .img-container .image-overlay:active {
    opacity: 1;
  }
  .img-container .image-overlay:active:before,
  .img-container .image-overlay:active:after {
    opacity: 1;
    transform: scale(1);
  }
}
.img-container .image-overlay:before,
.img-container .image-overlay:after {
  display: block;
  position: absolute;
  content: "";
  border: 1px solid var(--border-color);
  border-radius: 6px;
  top: 1em;
  bottom: 1em;
  left: 1em;
  right: 1em;
  opacity: 0;
  transform: scale(1.5);
  transition: all 0.3s ease-in-out;
}
.img-container .image-overlay:after {
  border-left: none;
  border-right: none;
  bottom: 1em;
  top: 1em;
}
.img-container .image-overlay:before {
  border-top: none;
  border-bottom: none;
  bottom: 1em;
  top: 1em;
}
.img-container.thumbnail {
  padding: 0.25rem;
  border: 1px solid var(--border-color);
  background-color: var(--thumb-background);
  display: flex;
  flex-direction: column;
  gap: 6px;
}
.img-container.thumbnail img {
  flex: 1;
  border-radius: calc(var(--img-border-radius)/2);
}
.img-container.thumbnail .title {
  font-weight: 600;
  line-height: 1;
  padding: 0.5rem;
  border-bottom: 1px solid var(--border-color);
}
.img-container.thumbnail .description {
  font-size: 13px;
  line-height: 1.2;
  margin-top: 0.25rem;
  padding: 0 0.5rem 0.5rem;
}
figure {
  margin: 0 0 1rem;
}
figure img {
  width: 100%;
  height: auto;
  display: block;
  position: relative;
  vertical-align: middle;
  background-color: transparent;
  transition: all 0.3s ease-in-out;
}
.flip-image-horizontal {
  transform: scaleX(-1);
}
.flip-image-vertical {
  transform: scaleY(-1);
}
.flip-image {
  transform: scale(-1);
}
.fit-cover {
  object-fit: cover;
}
.fit-contain {
  object-fit: contain;
}
.fit-fill {
  object-fit: fill;
}
.fit-scale-down {
  object-fit: scale-down;
}
.fit-none {
  object-fit: none;
}
