.charms {
  display: block;
  position: fixed;
  z-index: 1090;
  background-color: #1d1d1d;
  color: #ffffff;
  transition: transform 0.3s ease;
  padding: 16px;
}
.charms.top-side {
  bottom: 100%;
  width: 100%;
  left: 0;
}
.charms.top-side.open {
  transform: translateY(100%);
}
.charms.bottom-side {
  top: 100%;
  left: 0;
  width: 100%;
}
.charms.bottom-side.open {
  transform: translateY(-100%);
}
.charms.left-side {
  top: 0;
  bottom: 0;
  right: 100%;
  width: auto;
}
.charms.left-side.open {
  transform: translateX(100%);
}
.charms.right-side {
  top: 0;
  bottom: 0;
  width: auto;
  left: 100%;
}
.charms.right-side.open {
  transform: translateX(-100%);
}
.charm-tile,
.charm-notify {
  display: block;
  position: relative;
  cursor: pointer;
}
.charm-tile {
  width: 90px;
  height: 66px;
  padding: 4px;
  background-color: #50727C;
  color: #ffffff;
  margin: 2px;
  flex-shrink: 0;
  flex-grow: 0;
}
.charm-tile .icon {
  position: absolute;
  top: 4px;
  left: 4px;
  width: 16px;
  height: 16px;
  font-size: 16px;
}
.charm-tile .caption {
  margin-top: 30px;
  font-size: 10px;
  display: block;
}
.charm-tile.active {
  background-color: #126278;
}
.charm-notify {
  height: auto;
  background-color: #2D6070;
  color: #ffffff;
  padding: 24px;
}
.charm-notify .icon {
  position: absolute;
  left: 16px;
  top: 24px;
  width: 48px;
  height: 48px;
  font-size: 40px;
}
.charm-notify .title,
.charm-notify .content,
.charm-notify .secondary {
  display: block;
  position: relative;
  margin-left: 52px;
  line-height: 1;
}
.charm-notify .title {
  font-weight: bold;
  margin-bottom: 6px;
}
.charm-notify .content {
  font-size: 14px;
  line-height: 1.2;
}
.charm-notify .secondary {
  font-size: 10px;
  margin-top: 8px;
}
* + .charm-notify {
  margin-top: 4px;
}
