:root {
  --card-header-background: #fbfbfb;
  --card-header-color: #191919;
  --card-footer-background: #fbfbfb;
  --card-footer-color: #191919;
  --card-background: #ffffff;
  --card-color: #191919;
  --card-avatar-border-color: #ffffff;
  --card-border-radius: 6px;
  --card-button-border-color: var(--border-color);
}
.dark-side {
  --card-header-background: #282c35;
  --card-header-color: #fbfbfb;
  --card-footer-background: #282c35;
  --card-footer-color: #fbfbfb;
  --card-background: #2b2d30;
  --card-color: #dbdfe7;
  --card-avatar-border-color: #414245;
  --card-button-border-color: var(--border-color);
}
.card {
  display: flex;
  flex-flow: column nowrap;
  position: relative;
  font-size: 14px;
  margin: 8px auto;
  border: 1px solid var(--border-color);
  border-radius: var(--card-border-radius);
  background: var(--card-background);
  color: var(--card-color);
}
.card-header {
  border-bottom: 1px var(--border-color) solid;
  padding: 0.5rem 1rem;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background: var(--card-header-background);
  color: var(--card-header-color);
  border-top-left-radius: var(--card-border-radius);
  border-top-right-radius: var(--card-border-radius);
  overflow: hidden;
}
.card-header .buttons {
  margin-left: auto;
  display: flex;
  flex-flow: row nowrap;
}
.card-header .button {
  width: 24px;
  height: 24px;
  border-radius: 3px;
  border: 1px solid var(--card-button-border-color);
  padding: 0;
  margin-right: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card-header .button:last-child {
  margin-right: 0;
}
.card-header .avatar {
  float: left;
  width: 2.75rem;
  height: 2.75rem;
  overflow: hidden;
}
.card-header .avatar img {
  width: 100%;
  min-height: 100%;
}
.card-header .name {
  margin-left: 3.375rem;
  font-weight: 500;
}
.card-header .date {
  margin-left: 3.375rem;
  font-size: 0.8125rem;
  font-weight: 500;
  color: var(--body-color-secondary);
}
.card-content {
  display: flex;
  background-color: var(--card-background);
  color: var(--card-color);
  padding: 1rem;
}
.card-footer {
  border-top: 1px var(--border-color) solid;
  padding: 0.5rem 1rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background: var(--card-footer-background);
  color: var(--card-footer-color);
  border-bottom-left-radius: var(--card-border-radius);
  border-bottom-right-radius: var(--card-border-radius);
  overflow: hidden;
}
.card-footer .button {
  margin-right: 4px;
}
.card-footer .button:last-child {
  margin-right: 0;
}
.card.image-header .card-header {
  height: 9rem;
  vertical-align: bottom;
  background-size: cover;
  background-position: center;
  align-items: flex-start;
  justify-content: flex-end;
}
.card.image-header .card-header .avatar {
  border-radius: 50%;
  border: 2px solid var(--card-avatar-border-color);
}
* + .card {
  margin-top: 1rem;
}
.card.flex-card {
  display: flex;
  flex-flow: column;
}
.card.flex-card .card-header,
.card.flex-card .card-footer {
  height: 57px;
  flex-shrink: 0;
  flex-grow: 0;
}
.card.flex-card .card-footer {
  margin-top: auto;
}
