:root {
  --spinner-border-radius: 4px;
}
.spinner {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 4px;
  cursor: text;
  position: relative;
  height: 36px;
  line-height: 36px;
  border-radius: var(--spinner-border-radius);
}
.spinner input {
  border: none!important;
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
  line-height: 36px;
  font-size: 16px;
  padding: 0 4px!important;
  text-align: center;
}
.spinner input:focus {
  box-shadow: none!important;
}
.spinner .button {
  width: 25px;
  min-width: 25px;
  height: 25px;
  text-align: center;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  padding: 0;
  user-select: none;
}
.spinner .spinner-button-plus {
  order: 3;
}
.spinner .spinner-button-minus {
  order: 1;
}
.spinner input {
  order: 2;
}
.spinner.buttons-left .spinner-button-plus {
  order: 2;
  margin-left: 2px;
}
.spinner.buttons-left .spinner-button-minus {
  order: 1;
}
.spinner.buttons-left input {
  order: 3;
  text-align: right;
}
.spinner.buttons-right .spinner-button-plus {
  order: 3;
  margin-left: 2px;
}
.spinner.buttons-right .spinner-button-minus {
  order: 2;
}
.spinner.buttons-right input {
  order: 1;
  text-align: left;
}
.spinner.input-large {
  height: 50px;
  line-height: 50px;
}
.spinner.input-large input {
  font-size: 1.3rem;
}
.spinner.input-large .spinner-button {
  height: 48px;
  width: 48px;
  min-width: 48px;
  line-height: 48px;
  font-size: 1.3rem;
}
.spinner.input-large .spinner-button [class*=mif-] {
  font-size: 1.3rem;
}
.spinner.input-small {
  height: 28px;
  line-height: 28px;
}
.spinner.input-small input {
  font-size: 0.9rem;
}
.spinner.input-small .spinner-button {
  height: 22px;
  width: 22px;
  min-width: 22px;
  line-height: 22px;
  font-size: 0.9rem;
}
.spinner.input-small .spinner-button [class*=mif-] {
  font-size: 0.9rem;
}
