.sidenav-m3 {
  display: block;
  position: relative;
  list-style: none inside;
  height: 100%;
  width: 220px;
  margin: 0;
  padding: 0;
  transition: all 0.3s ease-in-out;
  background-color: #f8f8f8;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  overflow: -moz-scrollbars-none;
}
.sidenav-m3::-webkit-scrollbar {
  display: none!important;
}
.sidenav-m3 li,
.sidenav-m3 a {
  position: relative;
  display: block;
  color: inherit;
}
.sidenav-m3 > li {
  background: transparent;
}
.sidenav-m3 > li.stick-left::before,
.sidenav-m3 > li.stick-right::before {
  content: "";
  display: block;
  position: absolute;
  width: 6px;
  height: 44px;
  border-radius: 0.25rem 0 0 0.25rem;
  background-color: inherit;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}
.sidenav-m3 > li.stick-right::before {
  left: 100%;
  border-radius: 0 0.25rem 0.25rem 0;
  transform: translateX(0);
}
.sidenav-m3 a {
  padding: 0.625rem 1rem 0.625rem 3rem;
  text-decoration: none;
  background-color: #f8f8f8;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.sidenav-m3 a:hover {
  text-decoration: none;
}
.sidenav-m3 li + li {
  border-top: 1px solid var(--border-color);
}
.sidenav-m3 .icon {
  width: 24px;
  height: 24px;
  font-size: 22px;
  line-height: 24px;
  vertical-align: middle;
  text-align: center;
  position: absolute;
  left: 0.625rem;
  bottom: 12px;
}
.sidenav-m3 .title {
  padding: 20px 20px 10px 20px;
  font-size: 24px;
  border: 0;
}
.sidenav-m3 .d-menu {
  position: relative;
  box-shadow: none;
  background-color: inherit;
  width: 100%;
  float: none;
  border-radius: 0!important;
  padding: 0;
  margin: 0;
}
.sidenav-m3 .d-menu li:not(:first-child) {
  border-top: 1px solid var(--border-color);
}
.sidenav-m3 .d-menu a {
  border-radius: 0!important;
}
.sidenav-m3[dir=rtl] a {
  padding-right: 3rem;
  padding-left: 1rem;
}
.sidenav-m3[dir=rtl] .icon {
  left: auto;
  right: 0.625rem;
}
.sidenav-m3[dir=rtl] .dropdown-toggle {
  padding-left: 1.5rem !important;
  padding-right: 3rem!important;
}
.sidenav-m3[dir=rtl] .dropdown-toggle::before {
  left: 1rem;
  margin-left: 0;
}
