.container,
.container-fluid,
section,
header,
footer,
aside {
  margin: 0 auto;
  position: relative;
}
.container::after,
.container-fluid::after,
section::after,
header::after,
footer::after,
aside::after {
  display: block;
  clear: both;
  content: "";
}
.container {
  width: 100%;
  padding-right: 12px;
  padding-left: 12px;
  container: container / inline-size;
}
.container-fluid {
  width: 100%;
  max-width: none;
  padding-right: 12px;
  padding-left: 12px;
}
@media (min-width: 360px) and (max-width: 576px) {
  .container {
    max-width: 98%;
  }
}
@media (min-width: 577px) and (max-width: 768px) {
  .container {
    max-width: 95%;
  }
}
@media (min-width: 769px) and (max-width: 992px) {
  .container {
    max-width: 90%;
  }
}
@media (min-width: 993px) and (max-width: 1200px) {
  .container {
    max-width: 90%;
  }
}
@media (min-width: 1201px) {
  .container {
    max-width: 80%;
  }
}
.container-fs {
  max-width: 0;
}
.container-xs {
  max-width: 360px;
}
.container-sm {
  max-width: 576px;
}
.container-ld {
  max-width: 640px;
}
.container-md {
  max-width: 768px;
}
.container-lg {
  max-width: 992px;
}
.container-xl {
  max-width: 1200px;
}
.container-xxl {
  max-width: 1400px;
}
.container-xxxl {
  max-width: 2000px;
}
.container-max {
  width: 100%;
}
.container-query {
  container-name: container-query;
  container-type: inline-size;
}
