:root {
  --input-autocomplete-background: #ffffff;
  --input-autocomplete-color: #191919;
  --input-autocomplete-selected-color: #468cff;
  --input-autocomplete-selected-background: transparent;
  --input-border-radius: 4px;
  --input-font-size: 14px;
}
.dark-side {
  --input-autocomplete-background: #2b2d30;
  --input-autocomplete-color: #dbdfe7;
  --input-autocomplete-selected-color: #ffc351;
  --input-autocomplete-selected-background: transparent;
}
.input {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  width: 100%;
  padding: 0;
  border-radius: var(--input-border-radius);
}
.input:hover input {
  border-color: transparent;
}
.input.focused {
  box-shadow: 0 0 0 3px var(--input-box-shadow);
}
.input input {
  border: none!important;
  order: 1;
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: 0;
  height: 34px;
  padding: 0 0.75rem;
  background: transparent;
  color: inherit;
  font-size: var(--input-font-size);
}
.input input:focus {
  box-shadow: none;
}
.input input[placeholder] {
  font-size: var(--input-font-size);
}
.input .button-group {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  padding: 0 2px;
}
.input .button {
  background-color: transparent;
  border-color: transparent;
  color: var(--input-color);
  height: 30px;
  width: 30px;
  border: 1px solid var(--input-border-color);
  font-size: var(--input-font-size);
  padding: 0;
}
.input .button:focus,
.input .button:active {
  box-shadow: none!important;
}
.input .input-clear-button,
.input .input-reveal-button,
.input .input-search-button {
  font-family: "Segoe UI Symbol", serif;
  background-color: transparent;
  font-size: var(--input-font-size);
}
.input .input-clear-button:hover,
.input .input-reveal-button:hover,
.input .input-search-button:hover {
  background-color: transparent;
}
.input.disabled .button-group button {
  background-color: transparent;
  color: var(--input-disabled-color);
}
.input .prepend {
  order: 1;
}
.input .prepend-options {
  order: 2;
}
.input input {
  order: 3;
}
.input .append-options {
  order: 4;
}
.input .button-group {
  order: 5;
}
.input .append {
  order: 6;
}
.input .prepend,
.input .append {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: var(--input-font-size);
  padding: 0 var(--input-font-size);
}
.input .prepend {
  border-top-left-radius: var(--input-border-radius);
  border-bottom-left-radius: var(--input-border-radius);
}
.input .append {
  border-top-right-radius: var(--input-border-radius);
  border-bottom-right-radius: var(--input-border-radius);
}
.input.pill-input,
input.pill-input {
  --input-border-radius: 1rem;
}
.input.pill-input .button,
input.pill-input .button {
  border-radius: 50%;
}
.input .autocomplete-list {
  margin: 0;
  padding: 0;
  list-style: none inside;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  overflow: hidden;
  overflow-y: auto;
  background-color: var(--input-autocomplete-background);
  color: var(--input-autocomplete-color);
  height: auto;
  border: 1px solid var(--border-color);
  z-index: 3;
}
.input .autocomplete-list .item {
  display: flex;
  position: relative;
  padding: 0 8px;
  cursor: pointer;
  align-items: center;
  justify-content: flex-start;
}
.input .autocomplete-list .item:hover {
  background-color: rgba(29, 29, 29, 0.1);
}
.input .autocomplete-list .item + .item {
  border-top: 1px solid var(--border-color);
}
.input .autocomplete-list strong {
  background: var(--input-autocomplete-selected-background);
  color: var(--input-autocomplete-selected-color);
}
.input.input-large,
input.input-large {
  --input-font-size: 22px;
  height: 50px;
}
.input.input-large input,
input.input-large input {
  height: 48px;
  padding: 0 10px;
  line-height: 48px;
}
.input.input-large .button,
input.input-large .button {
  height: 44px;
  width: 44px;
}
.input.input-large .button .input-clear-button,
input.input-large .button .input-clear-button,
.input.input-large .button .input-reveal-button,
input.input-large .button .input-reveal-button {
  width: 48px;
  padding: 0;
}
.input.input-large .append,
input.input-large .append,
.input.input-large .prepend,
input.input-large .prepend {
  height: 48px;
  line-height: 48px;
}
.input.input-large.pill-input,
input.input-large.pill-input {
  --input-border-radius: 1.5rem;
}
.input.input-small,
input.input-small {
  --input-border-radius: 2px;
  --input-font-size: 14px;
  height: 28px;
}
.input.input-small input,
input.input-small input {
  height: 26px;
  padding: 0 4px;
  line-height: 26px;
}
.input.input-small .button,
input.input-small .button {
  height: 22px;
  width: 22px;
  padding: 0;
}
.input.input-small .append,
input.input-small .append,
.input.input-small .prepend,
input.input-small .prepend {
  height: 26px;
  line-height: 26px;
}
.input.input-small.pill-input,
input.input-small.pill-input {
  --input-border-radius: 0.8rem;
}
.input[dir=rtl],
.input.rtl {
  flex-flow: row-reverse nowrap;
}
.input[dir=rtl] input,
.input.rtl input {
  order: 3;
  direction: rtl;
}
.input[dir=rtl] .button-group,
.input.rtl .button-group {
  order: 2;
}
.input[dir=rtl] .prepend,
.input.rtl .prepend {
  order: 4;
}
.input[dir=rtl] .append,
.input.rtl .append {
  order: 1;
}
.input .badge {
  position: absolute;
  font-size: 9px;
  font-weight: 100;
  text-transform: uppercase;
  background-color: #0a87c1;
  color: #ffffff;
  border-radius: 6px;
  padding: 2px 4px;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}
.input:hover .badge {
  display: none;
}
.label-for-input {
  font-size: 12px;
  text-transform: uppercase;
}
.label-for-input.rtl {
  text-align: right;
}
