:root {
  --file-height: 36px;
  --file-button-background: #ebebeb;
  --file-button-background-hover: #dadada;
  --file-button-color: #191919;
  --file-drop-zone-border-size: 4px;
  --file-drop-zone-border-type: dashed;
  --file-drop-zone-border-color: #858585;
  --file-drop-zone-background: #f5f5f5;
  --file-drop-zone-color: #191919;
  --file-drop-zone-hover-color: #070707;
  --file-drop-zone-drop-color: #94ff6a;
}
.dark-side {
  --file-button-background: #3a3b40;
  --file-button-background-hover: #484850;
  --file-button-color: #f3fcff;
  --file-drop-zone-border-size: 4px;
  --file-drop-zone-border-type: dashed;
  --file-drop-zone-border-color: #858585;
  --file-drop-zone-background: #2b2d30;
  --file-drop-zone-color: #dbdfe7;
  --file-drop-zone-hover-color: #dbdfe7;
  --file-drop-zone-drop-color: #94ff6a;
}
.file input,
.drop-zone input,
.file-button input {
  width: 1px;
  height: 1px;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}
.file {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0;
  padding: 0;
}
.file .button {
  border: none;
  height: 30px;
  margin: 2px 2px 2px auto;
  background: var(--file-button-background);
  color: var(--file-button-color);
}
.file .button > * {
  height: 20px;
  line-height: 20px!important;
}
.file .button .icon {
  font-size: 20px;
  font-family: "Segoe UI Symbol", serif;
  display: flex;
  align-items: center;
  justify-content: center;
  object-fit: cover;
}
.file .button:hover {
  background: var(--file-button-background-hover);
}
.file .caption {
  line-height: 35px;
  display: block;
  padding: 0 0.5rem;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.file.disabled .button {
  pointer-events: none;
  opacity: 0.45;
  background: transparent;
}
.file .prepend {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
.file .prepend {
  order: 1;
}
.file .append {
  order: 4;
}
.file .caption {
  order: 2;
}
.file .button {
  order: 3;
}
.drop-zone {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  width: 100%;
  height: auto;
  padding: 40px 0;
  outline: var(--file-drop-zone-border-size) var(--file-drop-zone-border-type) var(--file-drop-zone-border-color);
  outline-offset: -12px;
  background: var(--file-drop-zone-background);
  color: var(--file-drop-zone-color);
  transition: all 0.5s;
  position: relative;
}
.drop-zone .icon {
  font-family: "Segoe UI Symbol", serif;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 64px;
  height: 64px;
  font-size: 64px;
  object-fit: cover;
}
.drop-zone .caption {
  margin-top: 10px;
  font-size: 14px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.drop-zone.focused,
.drop-zone:hover {
  outline-color: var(--file-drop-zone-hover-color) !important;
}
.drop-zone.drop-on {
  outline-color: #00b300;
}
.drop-zone .clear-button {
  position: absolute;
  top: 16px;
  right: 16px;
  font-family: "Segoe UI Symbol", serif;
  transform: scale(0);
  opacity: 0;
}
.drop-zone:hover .clear-button {
  opacity: 1;
  transform: scale(1);
}
.file[dir=rtl] .append,
.file.rtl .append {
  order: 1;
}
.file[dir=rtl] .prepend,
.file.rtl .prepend {
  order: 4;
}
.file[dir=rtl] .caption,
.file.rtl .caption {
  order: 3;
  text-align: right;
}
.file[dir=rtl] .button,
.file.rtl .button {
  order: 2;
  margin-left: 0;
  margin-right: auto;
}
