.resize-element {
  display: block;
  position: absolute;
  right: -1px;
  bottom: -1px;
  z-index: 101;
  cursor: nwse-resize;
  width: 12px;
  height: 12px;
}
.resize-element::after {
  position: absolute;
  display: block;
  border-bottom: 12px solid #00b500;
  border-left: 12px solid transparent;
  content: "";
  right: -2px;
  bottom: -2px;
}
.resize-element.resize-element-disabled {
  cursor: default;
}
.resize-element.resize-element-disabled::after {
  border-bottom-color: #ccc;
}
