:root {
  --slider-thumb-size: 18px;
  --slider-thumb-color: var(--color-blue);
  --slider-bar-color: #191919;
  --slider-buffer-color: #fefefe;
  --slider-back-color: #d5d5d5;
  --slider-fill-color: var(--color-cyan);
  --slider-thumb-border-color: var(--color-light-cyan);
}
.dark-side {
  --slider-thumb-color: var(--color-crimson);
  --slider-bar-color: #191919;
  --slider-buffer-color: #fefefe;
  --slider-back-color: #4e5055;
  --slider-fill-color: #ff145c;
  --slider-thumb-border-color: #ffffff;
}
.slider {
  display: block;
  position: relative;
  height: 2.125rem;
  background: transparent;
  cursor: default;
  width: 100%;
  border-radius: 4px;
}
.slider input {
  width: 1px;
  height: 1px;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}
.slider .backside,
.slider .complete,
.slider .buffer,
.slider .marker {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 4px;
}
.slider .marker {
  border-radius: 50%;
  padding: 0;
  transition: none;
}
.slider .backside {
  background-color: var(--slider-back-color);
  width: 100%;
  z-index: 1;
  height: 0.6rem;
}
.slider .buffer {
  z-index: 2;
  height: 0.3rem;
  background-color: var(--slider-buffer-color);
}
.slider .complete {
  z-index: 3;
  height: 0.6rem;
  background-color: var(--slider-fill-color);
}
.slider .marker {
  width: var(--slider-thumb-size);
  height: var(--slider-thumb-size);
  background: var(--slider-thumb-color);
  z-index: 4;
  outline: none;
  border: 2px solid var(--slider-thumb-border-color);
  cursor: pointer;
  overflow: visible;
}
.slider .marker:focus {
  box-shadow: 0 0 0 3px rgba(244, 244, 244, 0.45);
}
.slider .marker:active::after {
  display: block;
  content: "";
  position: absolute;
  width: 3.125rem;
  height: 3.125rem;
  border-radius: 50%;
  background-color: rgba(187, 187, 187, 0.5);
  opacity: 0.3;
  top: 50%;
  left: 50%;
  margin-top: -1.5625rem;
  margin-left: -1.5625rem;
}
.slider .hint {
  display: none;
  position: absolute;
  z-index: 5;
  width: auto!important;
  height: auto!important;
  padding: 0.25rem 0.5rem;
  background-color: #515151;
  color: #ffffff;
  white-space: nowrap;
}
.slider .hint::before {
  display: block;
  position: absolute;
  content: "";
  width: 6px;
  height: 6px;
  background-color: inherit;
}
.slider .hint.top-side {
  top: 0;
  left: 50%;
  transform: translateY(-100%) translateX(-50%);
  margin-top: -8px;
}
.slider .hint.top-side::before {
  top: 100%;
  left: 50%;
  transform: rotate(45deg) translateX(-50%);
  margin-top: -1px;
}
.slider .hint.bottom-side {
  left: 50%;
  transform: translateX(-50%);
  top: 100%;
  margin-top: 8px;
}
.slider .hint.bottom-side::before {
  top: 0;
  left: 50%;
  transform: rotate(45deg) translateX(-50%);
  margin-top: -1px;
}
.slider .hint.left-side {
  left: 0;
  top: 50%;
  transform: translateY(-50%) translateX(-100%);
  margin-left: -8px;
}
.slider .hint.left-side::before {
  top: 50%;
  right: 0;
  transform: translateY(-50%) rotate(45deg);
  margin-right: -3px;
}
.slider .hint.right-side {
  left: 100%;
  transform: translateY(-50%);
  top: 50%;
  margin-left: 8px;
}
.slider .hint.right-side::before {
  top: 50%;
  left: 0;
  transform: translateX(-50%) translateY(-50%) rotate(45deg);
}
.slider .marker.left-knob {
  left: 0;
}
.slider .marker.right-knob {
  left: auto;
  right: 0;
}
.slider.range .complete {
  width: 100%;
}
.slider.thin .backside,
.slider.thin .complete,
.slider.thin .buffer {
  height: 6px;
}
.slider.ultra-thin .backside,
.slider.ultra-thin .complete,
.slider.ultra-thin .buffer {
  height: 4px;
}
.slider.cycle-marker .marker {
  border-radius: 50%;
}
.slider.vertical-slider {
  width: 2.125rem;
  height: 100px;
}
.slider.vertical-slider .backside,
.slider.vertical-slider .complete,
.slider.vertical-slider .buffer,
.slider.vertical-slider .marker {
  top: auto;
  left: 50%;
  transform: translateY(0) translateX(-50%);
}
.slider.vertical-slider .backside,
.slider.vertical-slider .complete,
.slider.vertical-slider .buffer {
  width: 0.5rem;
  bottom: 0;
}
.slider.vertical-slider .backside {
  height: 100%;
}
.slider.vertical-slider .buffer {
  width: 0.3rem;
}
.slider.vertical-slider .marker {
  top: 100%;
  transform: translateY(-100%) translateX(-50%);
}
.slider.rounded .backside,
.slider.rounded .complete,
.slider.rounded .buffer,
.slider.rounded .marker {
  border-radius: 0.25rem;
}
.slider.disabled .marker {
  display: none;
}
.slider-min-max {
  display: block;
  position: relative;
}
.slider-min-max::after {
  display: block;
  clear: both;
  content: "";
}
.slider-min-max .slider-text-min,
.slider-min-max .slider-text-max {
  display: block;
}
.slider-min-max .slider-text-min {
  float: left;
}
.slider-min-max .slider-text-max {
  float: right;
}
