.master {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  color: #1d1d1d;
  background-color: #ffffff;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: visible;
}
.master .controls,
.master .pages,
.master .page {
  background-color: transparent;
  color: inherit;
}
.master .controls {
  display: flex;
  line-height: 40px;
  font-size: 20px;
  flex-flow: row nowrap;
  justify-content: space-between;
  margin: 20px 0 20px;
}
.master .controls .prev,
.master .controls .next,
.master .controls .title {
  display: block;
  position: relative;
  line-height: 40px;
  text-align: center;
  user-select: none;
}
.master .controls .prev,
.master .controls .next {
  width: 40px;
  height: 40px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.master .controls .prev.disabled,
.master .controls .next.disabled {
  color: #ccc;
}
.master .controls .title {
  width: 100%;
}
.master .controls .prev {
  order: 1;
}
.master .controls .next {
  order: 3;
}
.master .controls .title {
  order: 2;
}
.master .pages {
  position: relative;
  display: block;
  border-top: 1px solid var(--border-color);
  border-bottom: 1px solid var(--border-color);
  width: 100%;
  height: auto;
  min-width: 1rem;
}
.master .page {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  overflow: visible;
  padding: 2rem;
}
.master .controls-top {
  order: 1;
}
.master .controls-bottom {
  order: 3;
}
.master .pages {
  order: 2;
}
