:root {
  --keypad-background: var(--input-background);
  --keypad-color: var(--input-color);
  --keypad-border-color: #E8E8E8;
  --keypad-border-radius: 4px;
}
.dark-side {
  --keypad-background: var(--input-background);
  --keypad-color: var(--input-color);
  --keypad-border-color: #4A4D51;
}
.keypad {
  background: var(--keypad-background);
  color: var(--keypad-color);
  border: 1px solid var(--keypad-border-color);
  border-radius: var(--keypad-border-radius);
  position: relative;
}
.keypad input {
  border: 0;
}
.keypad .button-group {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  padding: 0;
  position: absolute;
  top: 2px;
  right: 2px;
}
.keypad .button {
  background-color: transparent;
  border-color: transparent;
  color: var(--input-color);
  height: 30px;
  width: 30px;
  border: 1px solid var(--input-border-color);
  font-size: var(--input-font-size);
  padding: 0;
}
.keypad .button:focus,
.keypad .button:active {
  box-shadow: none!important;
}
.keypad .keys {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  width: auto;
  float: left;
  background-color: inherit;
  color: inherit;
  border: none;
  padding: 0;
  user-select: none;
  z-index: 1000;
  flex-wrap: wrap;
  justify-content: flex-start;
  box-shadow: 0 1px 3px 0 var(--shadow-color-normal), 0 1px 2px -1px var(--shadow-color-normal);
}
.keypad .keys::before {
  content: '';
  position: absolute;
  background-color: inherit;
  width: 10px;
  height: 10px;
  border: 1px solid var(--keypad-border-color);
  z-index: 2;
  left: -5px;
  top: 50%;
  transform: translateY(-50%);
  margin-top: -5px;
  border-top-color: transparent;
  border-right-color: transparent;
  transform: rotate(45deg);
}
.keypad .keys.open {
  display: flex;
}
.keypad .key {
  display: block;
  position: relative;
  float: left;
  background-color: inherit;
  color: inherit;
  margin: 0;
  text-align: center;
  border: 1px solid var(--keypad-border-color);
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.15s ease-in-out;
  flex-shrink: 0;
}
.keypad .key:hover {
  box-shadow: 0 0 0 3px rgba(244, 244, 244, 0.45);
  transform: scale(1.1);
  z-index: 2;
}
.keypad .keys.right {
  left: 100%;
  transform: translateY(-50%);
  top: 50%;
  margin-left: -1px;
}
.keypad .keys.bottom {
  left: 50%;
  transform: translateX(-50%);
  top: 100%;
  margin-top: -1px;
}
.keypad .keys.bottom::before {
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  transform: rotate(135deg);
}
.keypad .keys.top {
  top: 0;
  left: 50%;
  transform: translateY(-100%) translateX(-50%);
  margin-top: 1px;
}
.keypad .keys.top::before {
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  transform: rotate(-45deg);
}
.keypad .keys.left {
  left: 0;
  transform: translateY(-50%) translateX(-100%);
  top: 50%;
  margin-left: 1px;
}
.keypad .keys.left::before {
  top: 50%;
  transform: translateY(-50%);
  left: 100%;
  margin-left: -5px;
  transform: rotate(225deg);
}
.keypad .keys.bottom-left {
  top: 100%;
  left: 0;
  margin-top: -1px;
}
.keypad .keys.bottom-left::before {
  top: 0;
  left: 0.625rem;
  transform: rotate(135deg);
}
.keypad .keys.bottom-right {
  top: 100%;
  left: 100%;
  transform: translateX(-100%);
  margin-top: -1px;
}
.keypad .keys.bottom-right::before {
  top: 0;
  left: 100%;
  margin-left: -1rem;
  transform: rotate(135deg);
}
.keypad .keys.top-left {
  top: 0;
  left: 0;
  transform: translateY(-100%);
  margin-top: 1px;
}
.keypad .keys.top-left::before {
  top: 100%;
  left: 0.625rem;
  transform: rotate(-45deg);
}
.keypad .keys.top-right {
  top: 0;
  left: 100%;
  transform: translateY(-100%) translateX(-100%);
  margin-top: 1px;
}
.keypad .keys.top-right::before {
  top: 100%;
  left: 100%;
  margin-left: -1rem;
  transform: rotate(-45deg);
}
