.marquee {
  display: block;
  position: relative;
  overflow: hidden;
}
.marquee .marquee__item {
  display: block;
  position: absolute;
  white-space: nowrap;
  color: inherit;
}
.marquee .marquee__item.moveLeftRight {
  top: 50%;
  transform: translateY(-50%);
  left: calc(100% + 20px);
}
.marquee .marquee__item.moveUpDown {
  left: 50%;
  transform: translateX(-50%);
  top: calc(100% + 20px);
}
