.bottom-sheet {
  background-color: #ffffff;
  display: block;
  z-index: 1030;
  max-width: 360px;
  left: 50%;
  list-style: none inside;
  padding: 0;
  margin: 0 0 0 -50%;
  width: 100%;
  top: 100%;
  position: fixed;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 6px 20px 0 rgba(0, 0, 0, 0.19), 0 8px 17px 0 rgba(0, 0, 0, 0.2);
}
.bottom-sheet::after {
  display: block;
  clear: both;
  content: "";
}
.bottom-sheet.wide-sm {
  max-width: 576px;
}
.bottom-sheet.wide-ld {
  max-width: 640px;
}
.bottom-sheet.wide-md {
  max-width: 768px;
}
.bottom-sheet.wide-lg {
  max-width: 992px;
}
.bottom-sheet.wide {
  max-width: 100%;
}
.bottom-sheet > .title {
  height: 56px;
  font-size: 16px;
  color: rgba(0, 0, 0, 0.87);
}
.bottom-sheet > li,
.bottom-sheet > .item {
  font-size: 16px;
  position: relative;
  height: 48px;
  padding-left: 56px;
  display: block;
  cursor: pointer;
  vertical-align: middle;
  line-height: 48px;
}
.bottom-sheet > li .icon,
.bottom-sheet > .item .icon {
  left: 16px;
  position: absolute;
  width: 24px;
  height: 24px;
  font-size: 24px;
  line-height: 24px;
  vertical-align: middle;
  text-align: center;
  top: 50%;
  margin-top: -12px;
  opacity: 0.8;
}
.bottom-sheet > li:hover,
.bottom-sheet > .item:hover,
.bottom-sheet > li.hovered,
.bottom-sheet > .item.hovered,
.bottom-sheet > li.active,
.bottom-sheet > .item.active {
  background-color: rgba(29, 29, 29, 0.1);
}
.bottom-sheet > .divider {
  height: 1px;
  background-color: var(--border-color);
}
.bottom-sheet.grid-style {
  padding: 16px;
}
.bottom-sheet.grid-style > li,
.bottom-sheet.grid-style > .item {
  width: 64px;
  height: 72px;
  line-height: normal;
  text-align: center;
  vertical-align: middle;
  display: block;
  float: left;
  margin: 8px;
  padding: 4px;
  font-size: 11px;
}
.bottom-sheet.grid-style > li .icon,
.bottom-sheet.grid-style > .item .icon {
  display: block;
  position: relative;
  width: 42px;
  height: 42px;
  font-size: 42px;
  margin: 0 0 8px -21px;
  padding: 0;
  top: 0;
  left: 50%;
  line-height: 1;
}
.bottom-sheet.grid-style > .divider {
  display: none;
}
.bottom-sheet.opened {
  transition: all 0.3s ease-in-out;
  transform: translate3d(0, -100%, 0);
}
.bottom-nav ~ .bottom-sheet {
  padding-bottom: 76px;
}
