:root {
  --tile-background: #ededed;
  --tile-color: #191919;
  --tile-border-color: var(--border-color);
  --tile-border-radius: 6px;
  --tile-badge-background: rgba(29, 29, 29, 0.1);
  --tile-badge-color: #191919;
  --tile-grid-gap: 10px;
}
.dark-side {
  --tile-background: #26282e;
  --tile-color: #ffffff;
  --tile-border-color: var(--border-color);
  --tile-badge-background: rgba(29, 29, 29, 0.1);
  --tile-badge-color: #ffffff;
}
.tile-small,
.tile-medium,
.tile-wide,
.tile-large,
.tile-app {
  display: block;
  background: var(--tile-background);
  color: var(--tile-color);
  width: 150px;
  height: 150px;
  box-shadow: inset 0 0 1px var(--tile-border-color);
  border-radius: var(--tile-border-radius);
  cursor: pointer;
  position: relative;
  overflow: hidden;
  user-select: none;
  max-width: none!important;
}
.tile-small {
  width: 70px;
  height: 70px;
}
.tile-medium {
  width: 150px;
  height: 150px;
}
.tile-wide {
  width: 310px;
  height: 150px;
}
.tile-large {
  width: 310px;
  height: 310px;
}
.tile-app {
  width: 44px;
  height: 44px;
}
.tile-small .icon,
.tile-medium .icon,
.tile-wide .icon,
.tile-large .icon,
.tile-app .icon {
  max-width: 33%;
  height: 33%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  font-size: 50px;
  line-height: 50px;
  z-index: 2;
}
.tile-small .branding-bar,
.tile-medium .branding-bar,
.tile-wide .branding-bar,
.tile-large .branding-bar,
.tile-app .branding-bar {
  height: 32px;
  line-height: 32px;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  padding: 0 10px 5px;
  font-size: 0.875rem;
  font-weight: 500;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  z-index: 2;
}
.tile-small .badge-top,
.tile-medium .badge-top,
.tile-wide .badge-top,
.tile-large .badge-top,
.tile-app .badge-top,
