:root {
  --shortcut-border-radius: 6px;
  --shortcut-background: #f8f8f8;
  --shortcut-color: #191919;
  --shortcut-background-hover: #dadada;
  --shortcut-color-hover: #474747;
}
.dark-side {
  --shortcut-background: #2e2e2e;
  --shortcut-color: #F8F8F8;
  --shortcut-background-hover: #373737;
  --shortcut-color-hover: #bfbfbf;
}
.shortcut {
  width: 84px;
  height: 84px;
  display: inline-flex;
  flex-direction: column;
  font-size: 0.9rem;
  transition: all 0.3s ease-in-out;
  background: var(--shortcut-background);
  color: var(--shortcut-color);
  cursor: pointer;
  white-space: nowrap;
  user-select: none;
  border: 1px solid transparent;
  position: relative;
  align-items: center;
  justify-content: center;
  outline: none;
  border-radius: var(--shortcut-border-radius);
  gap: 8px;
}
.shortcut .icon,
.shortcut .caption {
  display: block;
  line-height: 1;
}
.shortcut .icon {
  display: block;
  vertical-align: middle;
  width: 28px;
  height: 28px;
  font-size: 28px;
  order: 1;
  margin-top: 10px;
}
.shortcut .caption {
  color: inherit;
  order: 2;
  font-size: 12px;
}
.shortcut .badge {
  position: absolute;
  font-size: 0.6875rem;
  top: 0.25rem;
  right: 0.25rem;
  color: inherit;
  transform: none;
  background-color: inherit;
}
.shortcut.no-caption .icon {
  width: 32px;
  height: 32px;
  font-size: 32px;
  margin-top: 0;
}
@media (hover: hover) {
  .shortcut:hover {
    background: var(--shortcut-background-hover);
    color: var(--shortcut-color-hover);
  }
}
@media (hover: none) {
  .shortcut:active {
    background: var(--shortcut-background-hover);
    color: var(--shortcut-color-hover);
  }
}
.shortcut.focus,
.shortcut:focus {
  box-shadow: 0 0 0 3px rgba(244, 244, 244, 0.45);
}
.shortcut.outline {
  background: transparent none;
  border-width: 1px;
  border-color: var(--border-color);
}
.shortcut.outline:hover {
  background: var(--shortcut-background-hover);
  color: var(--shortcut-color-hover);
}
.shortcut:focus,
.shortcut:hover,
.shortcut:active {
  text-decoration: none;
}
.shortcut:active {
  outline: 0;
  box-shadow: 0 0 0 3px rgba(204, 204, 204, 0.45);
}
.shortcut.disabled,
.shortcut:disabled {
  opacity: 0.65;
}
.shortcut:active {
  box-shadow: none;
}
