.cube {
  display: block;
  position: relative;
  width: 320px;
  height: 310px;
  margin: 0 auto 30px;
  transform: rotateY(30deg);
}
.cube .side {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  position: absolute;
  overflow: hidden;
  width: 162px;
  height: 162px;
  bottom: 38px;
  left: 0;
}
.cube .side.left-side {
  transform: skewY(23deg) rotateX(10deg);
}
.cube .side.right-side {
  left: 160px;
  transform: skewY(-23deg) rotateY(10deg);
}
.cube .side.top-side {
  left: 80px;
  bottom: 154px;
  top: auto;
  transform: rotate(45deg) rotateZ(0) skew(-22deg, -22deg);
}
.cube .side .cube-cell {
  display: block;
  position: relative;
  width: 24px;
  height: 24px;
  margin: 8px;
  flex-grow: 0;
  flex-shrink: 0;
  background-color: #231F20;
  border: 1px solid #303030;
  box-shadow: 0 0 5px rgba(118, 118, 118, 0.8);
  transition: all 0.3s ease-in-out;
  line-height: 24px;
  text-align: center;
  color: #ffffff;
  font-size: 12px;
}
.cube .side .cube-cell.light {
  animation: pulsar-cell 2.5s 0s ease-out infinite;
  background-color: #c4122f;
  border-color: #c4122f;
  color: #1d1d1d;
  box-shadow: 0 0 10px #c4122f, 0 0 10px #c4122f, 0 0 10px #c4122f;
}
@keyframes pulsar-cell {
  0%,
  100% {
    box-shadow: 0 0 10px #c4122f, 0 0 10px #c4122f, 0 0 10px #c4122f;
  }
  50% {
    box-shadow: 0 0 10px rgba(196, 18, 47, 0.3), 0 0 10px rgba(196, 18, 47, 0.3), 0 0 10px rgba(196, 18, 47, 0.3);
  }
}
.cube .axis {
  height: 2px;
  width: 200px;
  position: absolute;
  background-color: #c4122f;
  bottom: 162px;
  left: 162px;
  transform-origin: 0 0 0;
}
.cube .axis::before,
.cube .axis::after {
  content: "";
  position: absolute;
  height: 1px;
  background-color: inherit;
  display: block;
}
.cube .axis.arrow::before,
.cube .axis.arrow::after {
  width: 16px;
  top: 0;
  right: 0;
  transform-origin: 100% 100% 0;
}
.cube .axis.arrow::before {
  transform: rotate(35deg);
}
.cube .axis.arrow::after {
  transform: rotate(-35deg);
  right: -1px;
}
.cube .axis.line::before,
.cube .axis.line::after {
  width: 16px;
  top: 0;
  right: 0;
  transform-origin: 100% 100% 0;
}
.cube .axis.line::before {
  transform: rotate(90deg);
}
.cube .axis.line::after {
  transform: rotate(-90deg);
  right: -1px;
}
.cube .axis.line.axis-y::before {
  transform: rotate(113deg);
}
.cube .axis.line.axis-y::after {
  transform: rotate(-67deg);
}
.cube .axis.line.axis-x::before {
  transform: rotate(67deg);
}
.cube .axis.line.axis-x::after {
  transform: rotate(-113deg);
}
.cube .axis.no-style::before,
.cube .axis.no-style::after {
  display: none;
}
.cube .axis.axis-x {
  transform: rotate(203deg);
}
.cube .axis.axis-y {
  bottom: 166px;
  transform: rotate(-23deg);
}
.cube .axis.axis-z {
  width: 190px;
  transform: rotate(90deg);
}
