.h-menu {
  list-style: none inside;
  display: block;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  color: #1d1d1d;
}
.h-menu::after {
  display: block;
  clear: both;
  content: "";
}
.h-menu > li,
.h-menu > li > a {
  display: block;
  position: relative;
}
.h-menu > li {
  background-color: inherit;
  color: inherit;
  float: left;
}
.h-menu > li:hover {
  background-color: rgba(29, 29, 29, 0.1);
}
.h-menu > li:hover > a,
.h-menu > li:hover .icon {
  color: inherit;
}
.h-menu > li > a {
  font-weight: normal;
  color: inherit;
  font-size: 1rem;
  outline: none;
  text-decoration: none;
  padding: 0.5rem 1rem;
  border: none;
  background-color: inherit;
  white-space: nowrap;
}
.h-menu > li > a:hover {
  text-decoration: none;
}
.h-menu > li > a.dropdown-toggle {
  padding-right: 2rem!important;
}
.h-menu > li > a.dropdown-toggle::before {
  margin-left: -1.25rem;
}
.h-menu.no-hover > li > a {
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  color: #6a6a6a;
}
.h-menu.no-hover > li:hover,
.h-menu.no-hover > li > a:hover {
  background-color: transparent;
  color: #1d1d1d;
}
.h-menu.large > li > a {
  padding: 1.125rem 1.625rem;
  font-size: 2rem;
}
.h-menu.large > li > a.dropdown-toggle {
  padding-right: 2.5rem !important;
}
.h-menu.large > li > a.dropdown-toggle::before {
  height: 0.625rem;
  width: 0.625rem;
  margin-left: -1.75rem;
}
.h-menu > .divider {
  display: none;
}
.h-menu.mega {
  position: relative;
}
.h-menu.mega > li {
  position: static;
}
.h-menu.mega > li.active-container {
  background-color: inherit;
}
.h-menu.mega > li.active-container a {
  background-color: inherit;
}
.h-menu.mega .mega-container {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  padding: 0;
  background-color: inherit;
  display: none;
  color: inherit;
  z-index: 1000;
  box-shadow: 2px 2px 2px 0 rgba(29, 29, 29, 0.4);
}
.h-menu.mega .mega-container * {
  color: inherit;
}
