:root {
  --chat-border-color: var(--border-color);
  --chat-background: #ffffff;
  --chat-color: #191919;
  --chat-message-background: #f5f5f5;
  --chat-message-color: #191919;
}
.dark-side {
  --chat-background: #343637;
  --chat-color: #dbdfe7;
  --chat-message-background: #4a4d51;
  --chat-message-color: #dbdfe7;
}
.chat {
  display: flex;
  flex-flow: column;
  border: 1px solid var(--border-color);
  background: var(--chat-background);
  color: var(--chat-color);
  position: relative;
  border-radius: 6px;
}
.chat .title,
.chat .messages,
.chat .message-input {
  display: block;
}
.chat .title {
  font-size: 18px;
  line-height: 1.2;
  padding: 8px;
  border-bottom: 1px solid var(--border-color);
}
.chat .messages {
  padding: 8px;
  border-bottom: 1px solid var(--border-color);
  height: 100%;
  overflow: hidden;
  overflow-y: auto;
}
.chat .message {
  display: flex;
  flex-flow: row wrap;
}
.chat .message .message-sender {
  font-weight: bold;
  font-size: 12px;
  order: 1;
  position: absolute;
  top: 6px;
  left: 6px;
}
.chat .message .message-time {
  font-size: 11px;
  order: 2;
  opacity: 0.5;
  position: absolute;
  left: 6px;
  bottom: 6px;
}
.chat .message .message-item {
  width: 100%;
  flex-shrink: 0;
  display: flex;
  flex-flow: row nowrap;
  margin: 2px 0 0;
  order: 3;
}
.chat .message .message-item .message-avatar {
  overflow: hidden;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  flex-shrink: 0;
  display: flex;
  order: 1;
  font-size: 36px;
  align-items: center;
  justify-content: center;
  object-fit: cover;
}
.chat .message .message-item .message-text {
  display: flex;
  align-items: center;
  border: 1px solid var(--chat-border-color);
  margin-left: 8px;
  order: 2;
  font-size: 14px;
  line-height: 1.2;
  padding: 30px 10px;
  min-width: 100px;
  max-width: 100%;
  background: var(--chat-message-background);
  color: var(--chat-message-color);
  position: relative;
  border-radius: 4px;
}
.chat .message .message-item .message-text-inner {
  width: 100%;
  overflow: hidden;
}
.chat .message .message-item .message-text::before {
  position: absolute;
  right: 100%;
  top: 14px;
  border: 6px solid transparent;
  border-right-color: inherit;
  content: ' ';
  height: 0;
  width: 0;
  pointer-events: none;
}
.chat .message .message-item .message-text.--next {
  padding-top: 10px;
}
.chat .message .message-item .message-text.--next::before {
  display: none;
}
.chat .message.right .message-item {
  justify-content: flex-end;
}
.chat .message.right .message-time {
  order: 1;
  text-align: left;
  left: auto;
  right: 6px;
}
.chat .message.right .message-sender {
  order: 2;
  left: auto;
  right: 6px;
}
.chat .message.right .message-avatar {
  order: 2;
}
.chat .message.right .message-text {
  order: 1;
  margin-left: 0;
  margin-right: 8px;
}
.chat .message.right .message-text::before {
  right: auto;
  left: 100%;
  border-right-color: transparent;
  border-left-color: inherit;
}
.chat .message.right .message-text.--next::before {
  display: none;
}
.chat .message-input {
  flex-shrink: 0;
  margin-top: auto;
  padding: 8px;
}
.chat .message-input.disabled {
  opacity: 0.5;
}
.chat .input-custom-button {
  width: auto;
  padding: 0 10px;
}
.chat .message.left + .message.right {
  margin-top: 10px;
}
.chat .message.right + .message.left {
  margin-top: 10px;
}
