:root {
  --button-border-radius: 4px;
  --button-pill-radius: 18px;
  --button-size: 36px;
  --button-font-size: 14px;
  --button-background: #ebebeb;
  --button-background-hover: #dadada;
  --button-color: #191919;
  --button-border-color: #ebebeb;
  --button-disabled-opacity: 0.65;
}
.dark-side {
  --button-background: #2b2d30;
  --button-background-hover: #333439;
  --button-color: #f3fcff;
  --button-border-color: #4e5157;
  --button-disabled-opacity: 0.25;
}
button,
input[type="button"],
input[type="submit"],
input[type="reset"],
.button,
.flat-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  user-select: none;
  padding: 0 var(--button-font-size);
  font-size: var(--button-font-size);
  height: var(--button-size);
  transition: all 0.15s ease-in-out;
  background-color: var(--button-background);
  color: var(--button-color);
  border: 1px solid var(--button-border-color);
  cursor: pointer;
  outline: none;
  position: relative;
  overflow: visible;
  border-radius: var(--button-border-radius);
  gap: 8px;
}
button:first-child,
input[type="button"]:first-child,
input[type="submit"]:first-child,
input[type="reset"]:first-child,
.button:first-child,
.flat-button:first-child {
  margin-left: 0;
}
button:last-child,
input[type="button"]:last-child,
input[type="submit"]:last-child,
input[type="reset"]:last-child,
.button:last-child,
.flat-button:last-child {
  margin-right: 0;
}
button.disabled,
input[type="button"].disabled,
input[type="submit"].disabled,
input[type="reset"].disabled,
.button.disabled,
.flat-button.disabled,
button:disabled,
input[type="button"]:disabled,
input[type="submit"]:disabled,
input[type="reset"]:disabled,
.button:disabled,
.flat-button:disabled {
  pointer-events: none;
  opacity: var(--button-disabled-opacity);
}
button .icon,
input[type="button"] .icon,
input[type="submit"] .icon,
input[type="reset"] .icon,
.button .icon,
.flat-button .icon {
  height: calc(var(--button-size)/2);
  font-size: calc(var(--button-font-size) * 1.3);
  display: flex;
  align-items: center;
  justify-content: center;
}
button.pill-button,
input[type="button"].pill-button,
input[type="submit"].pill-button,
input[type="reset"].pill-button,
.button.pill-button,
.flat-button.pill-button {
  border-radius: var(--button-pill-radius);
}
button:active,
input[type="button"]:active,
input[type="submit"]:active,
input[type="reset"]:active,
.button:active,
.flat-button:active,
button.focus,
input[type="button"].focus,
input[type="submit"].focus,
input[type="reset"].focus,
.button.focus,
.flat-button.focus,
button:focus,
input[type="button"]:focus,
input[type="submit"]:focus,
input[type="reset"]:focus,
.button:focus,
.flat-button:focus {
  text-decoration: none;
}
@media (hover: hover) {
  button:hover,
  input[type="button"]:hover,
  input[type="submit"]:hover,
  input[type="reset"]:hover,
  .button:hover,
  .flat-button:hover {
    background-color: var(--button-background-hover);
  }
}
@media (hover: none) {
