    color: var(--button-color);
  }
}
@media (hover: none) {
  button.flat:active,
  .button.flat:active,
  .flat-button:active {
    background-color: var(--button-background);
    color: var(--button-color);
  }
}
@keyframes spin-loader {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}
