  color: #ffffff !important;
  border-color: #d1c5a1 !important;
}
@media (hover: hover) {
  .button-sand:hover {
    background: #d1c5a1 !important;
    color: #ffffff !important;
  }
}
@media (hover: none) {
  .button-sand:active {
    background: #d1c5a1 !important;
    color: #ffffff !important;
  }
}
.button-seashell,
.button-champagne,
.button-yellow,
.button-lime {
  color: #191919 !important;
}
