:root {
  --tool-button-border-radius: 4px;
  --tool-button-background: #F8F8F8;
  --tool-button-color: #191919;
  --tool-button-background-hover: #dcdcdc;
  --tool-button-color-hover: #474747;
  --tool-button-border-color: #E8E8E8;
}
.dark-side {
  --tool-button-background: #2e2e2e;
  --tool-button-color: #F8F8F8;
  --tool-button-background-hover: #373737;
  --tool-button-color-hover: #bfbfbf;
  --tool-button-border-color: #4A4D51;
}
.tool-button {
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--tool-button-background);
  color: var(--tool-button-color);
  user-select: none;
  border: 1px solid transparent;
  border-radius: var(--tool-button-border-radius);
  transition: all 0.3s ease-in-out;
  padding: 0;
  cursor: pointer;
  outline: none;
  margin: 0 2px;
  position: relative;
  font-size: 14px;
}
.tool-button [class*=mif],
.tool-button .icon,
.tool-button img {
  height: 16px;
  width: 16px;
  font-size: 16px;
}
.tool-button.text-button {
  width: auto;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}
.tool-button:focus,
.tool-button:hover {
  text-decoration: none;
}
.tool-button:active {
  outline: 0;
  box-shadow: none;
}
.tool-button.outline {
  background: transparent none;
  border-width: 1px;
  border-color: var(--tool-button-border-color);
}
.tool-button:hover {
  background: var(--tool-button-background-hover);
  color: var(--tool-button-color-hover);
}
.tool-button.disabled,
.tool-button:disabled {
  opacity: 0.65;
}
.toolbar {
  display: inline-flex;
  position: relative;
  padding: 0;
  flex-flow: row nowrap;
}
.toolbar.no-divider::before {
  display: none;
}
.toolbar::before {
  position: absolute;
  content: "";
  width: 4px;
  height: 32px;
  top: 0;
  left: 0;
  background: var(--tool-button-background);
  cursor: default;
  display: none;
}
.toolbar::after {
  content: attr(data-caption);
  position: absolute;
  left: 2px;
  bottom: 100%;
  font-size: 8px;
  text-transform: uppercase;
}
.toolbar.movable {
  padding-left: 6px;
}
.toolbar.movable::before {
  display: block;
  cursor: move;
}
.toolbar.vertical {
  flex-flow: column nowrap;
  padding: 0.5725rem 0 0 0;
  width: 32px;
  margin-left: 0;
  margin-top: 4px;
}
.toolbar.vertical::before {
  width: 32px;
  height: 4px;
}
.toolbar.vertical::after {
  transform: rotate(-90deg);
  right: 100%;
  top: auto;
}
.toolbar.vertical .tool-button {
  margin: 2px 0;
}
