.head-bar {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #ffffff;
  color: #1d1d1d;
  height: 56px;
  z-index: 1030;
  padding: 0 4px;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.16), 0 2px 5px 0 rgba(0, 0, 0, 0.26);
}
.head-bar .app-title {
  height: 48px;
  display: inline-block;
  vertical-align: middle;
  margin: 0 4px;
  line-height: 48px;
  color: inherit;
  font-size: 20px;
  font-weight: 100;
}
.head-bar .app-title:nth-child(1) {
  padding-left: 10px;
}
.head-bar .header-container {
  height: 100%;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}
.head-bar .tool-button {
  margin: 0 4px;
  background-color: inherit;
  color: inherit;
}
.head-bar .tool-button .icon {
  width: 24px;
  height: 24px;
  font-size: 24px;
}
.head-bar.more {
  height: 112px;
}
.head-bar.more .app-title {
  position: absolute;
  bottom: 22px;
  left: 74px;
  height: 28px;
  font-size: 24px;
}
.head-bar .header-container .input {
  float: left;
  margin-left: 20px;
  width: calc(100% - 80px);
  border: none;
}
.head-bar .header-container .input input {
  border: none;
}
.head-bar .header-container .input input,
.head-bar .header-container .input input::-webkit-input-placeholder {
  color: white;
}
