.clear-float::after {
  display: block;
  clear: both;
  content: "";
}
.place-right,
.float-right {
  float: right !important;
}
.place-left,
.float-left {
  float: left !important;
}
.place-none,
.float-none,
.no-float {
  float: none !important;
}
