:root {
  --tag-background: #f8f8f8;
  --tag-color: #191919;
  --tag-action-background: #dcdcdc;
  --tag-action-color: #191919;
}
.dark-side {
  --tag-background: #53575c;
  --tag-color: #f3fcff;
  --tag-action-background: #2b2d30;
  --tag-action-color: #ffffff;
}
.tag {
  display: inline-flex;
  justify-content: space-between;
  align-items: flex-start;
  position: relative;
  margin: 2px;
  padding: 0;
  height: 26px;
  line-height: 26px;
  background: var(--tag-background);
  color: var(--tag-color);
  cursor: default;
  border-radius: 4px;
  flex-wrap: nowrap;
}
.tag .title {
  display: flex;
  align-items: center;
  position: relative;
  font-size: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 0 0.5rem;
  white-space: nowrap;
}
.tag .action {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 26px;
  height: 100%;
  line-height: 22px;
  text-align: center;
  cursor: pointer;
  background: var(--tag-action-background);
  color: var(--tag-action-color);
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  transition: all 0.15s ease-in-out;
  font-size: 12px;
}
.tag .action:hover {
  font-size: 1.4rem;
}
.tag.short-tag .title {
  max-width: 120px;
}
.tag.small {
  margin: 2px 2px;
  height: 18px;
  line-height: 18px;
}
.tag.small .title {
  font-size: 0.7rem;
  line-height: 18px;
}
.tag.small .action {
  width: 18px;
  height: 18px;
  line-height: 18px;
  font-size: 0.8rem;
}
.tag.large {
  margin: 2px 2px;
  height: 36px;
  line-height: 36px;
}
.tag.large .title {
  font-size: 1.3rem;
  line-height: 36px;
}
.tag.large .action {
  width: 35px;
  height: 35px;
  line-height: 33px;
  font-size: 1.6rem;
}
.tag.static {
  background: #d4f0ff;
  color: #1d1d1d;
}
.tag.static .action {
  display: none!important;
