.sidenav-simple {
  display: block;
  position: relative;
  list-style: none inside;
  height: 100%;
  width: 52px;
  overflow-x: hidden;
  overflow-y: auto;
  margin: 0;
  padding: 0;
  transition: all 0.3s ease-in-out;
  background-color: #f8f8f8;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  overflow: -moz-scrollbars-none;
}
.sidenav-simple::-webkit-scrollbar {
  display: none!important;
}
.sidenav-simple li,
.sidenav-simple a {
  display: block;
  position: relative;
  height: 52px;
  line-height: 52px;
  background-color: inherit;
  color: inherit;
  width: 100%;
}
.sidenav-simple a {
  padding: 0.625rem 0 0.625rem 0;
  line-height: 0.875rem;
  text-decoration: none;
}
.sidenav-simple a:hover {
  text-decoration: none;
}
.sidenav-simple .icon {
  width: 28px;
  height: 28px;
  font-size: 26px;
  line-height: 28px;
  vertical-align: middle;
  text-align: center;
  position: absolute;
  left: 0.625rem;
  top: 0.635rem;
}
.sidenav-simple .title {
  margin: 0;
  white-space: nowrap;
  font-size: 0.875rem;
  text-transform: uppercase;
  display: none;
  line-height: 52px;
}
.sidenav-simple li:hover {
  background-color: rgba(29, 29, 29, 0.1);
}
.sidenav-simple li:active {
  background-color: #456c7e;
  color: #ffffff;
}
.sidenav-simple .active,
.sidenav-simple li.active:hover {
  background-color: #94b6ff;
  color: #ffffff;
}
.sidenav-simple[dir=rtl] .icon {
  left: 0.725rem;
}
@media screen and (min-width: 0) {
  .sidenav-simple-expand-fs {
    width: 220px;
  }
  .sidenav-simple-expand-fs a {
    padding: 0 1rem 0 3.75rem;
  }
  .sidenav-simple-expand-fs .title {
    display: block;
  }
  .sidenav-simple-expand-fs[dir=rtl] a {
    padding: 0.625rem 3.75rem 0.625rem 1rem;
  }
  .sidenav-simple-expand-fs[dir=rtl] .icon {
    left: auto;
    right: 0.625rem;
  }
}
