.media-player {
  display: block;
  position: relative;
  width: 100%;
  background: #000;
  height: auto;
  overflow: hidden;
  z-index: 1;
}
.media-player video {
  width: 100%;
  height: 100%;
  z-index: 1;
}
.media-player .logo {
  display: block;
  position: absolute;
  top: 1.25rem;
  right: 1.25rem;
  z-index: 3;
  height: 32px;
}
.media-player .logo img {
  height: 32px;
}
.media-player .preloader {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.media-player .preloader.show {
  display: block;
}
.media-player .controls {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: auto;
  z-index: 2147483647;
  background-color: rgba(34, 34, 34, 0.5);
}
.media-player .controls.outside {
  position: relative;
}
.media-player .stream {
  display: block;
  padding: 0.625rem;
  order: 5;
  width: 100%;
  position: relative;
}
.media-player .stream .slider {
  height: 0.875rem;
}
.media-player .load-audio {
  padding: 0;
  display: block;
  position: absolute;
  bottom: 4px;
  left: 0.625rem;
  width: calc(100% - 1.25rem);
}
.media-player button:focus {
  box-shadow: none!important;
}
.media-player .controls > button,
.media-player .info-box,
.media-player .volume {
  background-color: inherit;
  color: #555555;
  flex-shrink: 0;
  height: 36px;
  text-align: center;
}
.media-player .controls > button:hover,
.media-player .info-box:hover,
.media-player .volume:hover {
  color: #ffffff;
}
.media-player .controls > button.active,
.media-player .info-box.active,
.media-player .volume.active {
  background-color: rgba(111, 111, 111, 0.5);
}
.media-player .controls .button {
  display: flex;
  justify-content: center;
  align-items: center;
}
.media-player .loop {
  order: 1;
  display: none;
}
.media-player .play {
  order: 2;
}
.media-player .stop {
  order: 3;
  display: none;
}
.media-player .info-box {
  position: relative;
  width: auto;
  display: block;
  order: 4;
  background: rgba(34, 34, 34, 0.5);
  color: #ffffff;
  padding: 0.625rem;
  font-size: 0.6875rem;
  text-align: center;
  height: 36px;
  flex-shrink: 1;
  white-space: nowrap;
  border: none;
}
.media-player .mute {
  order: 6;
}
.media-player .volume {
  order: 7;
  width: 96px;
  padding: 0 0.625rem;
  background: rgba(34, 34, 34, 0.5);
  display: none;
}
.media-player .full {
  order: 8;
}
:-webkit-full-screen {
  width: 100%;
  height: 100%;
  z-index: 2147483647;
}
:-ms-fullscreen {
  width: 100%;
}
.media-player.full-screen {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2147483647;
  max-height: 100%!important;
  height: 100%!important;
}
.media-player.light {
  background-color: #f8f8f8;
}
.media-player.light .controls {
  background-color: inherit;
}
.media-player.light .controls .info-box,
.media-player.light .controls .volume {
  background-color: inherit;
  color: #1d1d1d;
}
.media-player.light .slider .buffer {
  height: 2px!important;
  background-color: #36464e;
}
.media-player.light .controls > button.active {
  background-color: #dfdfdf;
}
.audio-player {
  overflow: visible;
}
.audio-player .controls {
  position: relative;
}
@media screen and (min-width: 576px) {
  .media-player .volume,
  .media-player .loop,
  .media-player .stop {
    display: flex;
  }
}
