:root {
  --remark-border-radius: 6px;
  --remark-border-color: var(--border-color);
  --remark-background: #ffffff;
  --remark-color: #191919;
}
.dark-side {
  --remark-border-color: var(--border-color);
  --remark-background: #26282e;
  --remark-color: #dbdfe7;
}
.remark {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  position: relative;
  border: 1px solid;
  border-left: 4px solid;
  border-color: var(--remark-border-color);
  border-radius: var(--remark-border-radius);
  color: var(--remark-color);
  background: var(--remark-background);
  padding: 1rem;
  font-size: 14px;
  margin: 1rem 0;
  cursor: default;
  overflow: hidden;
}
.remark .icon {
  margin-right: 1rem;
  font-size: 32px;
  width: 32px;
  height: 32px;
  position: absolute;
  top: 50%;
  left: 1rem;
  transform: translateY(-50%);
}
.remark:has(.icon) {
  min-height: 40px;
  padding-left: 60px;
}
